@import '../libs/font-awesome/font-awesome.min.css';
@import '../libs/base-css/reset.css';
@import '../libs/base-css/base.css';
@font-face {
  font-family: 'ProximaNova';
  src: url("../fonts/ProximaNova/ProximaNova-Bold/ProximaNova-Bold.eot");
  src: url("../fonts/ProximaNova/ProximaNova-Bold/ProximaNova-Bold.eot?#iefix") format('embedded-opentye'), url("../fonts/ProximaNova/ProximaNova-Bold/ProximaNova-Bold.woff") format('woff'), url("../fonts/ProximaNova/ProximaNova-Bold/ProximaNova-Bold.ttf") format('truetype');
  font-style: normal;
  font-weight: bold;
}
@font-face {
  font-family: 'ProximaNova';
  src: url("../fonts/ProximaNova/ProximaNova-Regular/ProximaNova-Regular.eot");
  src: url("../fonts/ProximaNova/ProximaNova-Regular/ProximaNova-Regular.eot?#iefix") format('embedded-opentye'), url("../fonts/ProximaNova/ProximaNova-Regular/ProximaNova-Regular.woff") format('woff'), url("../fonts/ProximaNova/ProximaNova-Regular/ProximaNova-Regular.ttf") format('truetype');
  font-style: normal;
  font-weight: normal;
}
@font-face {
  font-family: 'FontAwesome';
  src: url("../fonts/font-awesome/fontawesome-webfont.eot");
  src: url("../fonts/font-awesome/fontawesome-webfont.eot?#iefix") format('embedded-opentye'), url("../fonts/font-awesome/fontawesome-webfont.woff") format('woff'), url("../fonts/font-awesome/fontawesome-webfont.ttf") format('truetype');
  font-style: normal;
  font-weight: normal;
}
.wrapper {
  display: flex;
  min-height: 100vh;
  margin: 0 auto;
}
.wrapper .game-place {
  height: calc(100vh - 30px);
  width: calc(100vh * 1.54 - 30px);
  border: 1px solid #000;
  box-sizing: border-box;
  margin: 15px 10px;
  flex-shrink: 0;
  position: relative;
}
.wrapper .game-place .top ul,
.wrapper .game-place .middle ul,
.wrapper .game-place .bottom ul {
  list-style: none;
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
}
.wrapper .game-place .top li,
.wrapper .game-place .middle li,
.wrapper .game-place .bottom li {
  border: 1px solid #808080;
  box-sizing: border-box;
}
.wrapper .game-place .top li,
.wrapper .game-place .bottom li {
  width: calc(70% / 11);
}
.wrapper .game-place .top li.square,
.wrapper .game-place .bottom li.square {
  width: 15%;
}
.wrapper .game-place .top li.square>div,
.wrapper .game-place .bottom li.square>div {
  position: relative;
  width: 100%;
  padding-bottom: 100%;
}
.wrapper .game-place .top li.square>div>div,
.wrapper .game-place .bottom li.square>div>div {
  width: 100%;
  height: 100%;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
}
.wrapper .game-place .middle {
  height: 53%;
}
.wrapper .game-place .middle .container {
  display: flex;
  flex-direction: row;
  height: 100%;
}
.wrapper .game-place .middle .container .left,
.wrapper .game-place .middle .container .right {
  width: 15%;
  display: block;
  border: none;
}
.wrapper .game-place .middle .container .left>ul,
.wrapper .game-place .middle .container .right>ul {
  display: flex;
  flex-direction: column;
  height: 100%;
}
.wrapper .game-place .middle .container .left>ul>li.item,
.wrapper .game-place .middle .container .right>ul>li.item {
  height: 19%;
}
.wrapper .game-place .middle .container .left>ul>li.center,
.wrapper .game-place .middle .container .right>ul>li.center {
  height: 24%;
  width: 100%;
}
.wrapper .game-place .middle .container .center {
  width: 70%;
  display: flex;
  flex-direction: row;
}
.wrapper .game-place .middle .container .center .monitor {
  width: 70%;
  border-right: 2px solid #808080;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.wrapper .game-place .middle .container .center .monitor i {
  margin: 10px;
  font-size: 12px;
}
.wrapper .game-place .middle .container .center .monitor hr {
  margin: 10px 0;
}
.wrapper .game-place .middle .container .center .monitor .names input {
  border: 1px solid #000;
  margin: 5px 0;
}
.wrapper .game-place .middle .container .center .monitor .names input.error {
  outline: 1px solid #c40f13;
}
.wrapper .game-place .middle .container .center .monitor .timeOut {
  font-size: 50px;
  display: none;
}
.wrapper .game-place .middle .container .center .chat {
  width: 30%;
  padding: 5px;
  overflow: auto;
}
.wrapper .game-place .middle .container .center .chat p.info {
  font-size: 12px;
  color: #808080;
}
.wrapper .game-place .middle .container .center .chat p.trade {
  font-size: 12px;
  color: #009688;
}
.wrapper .game-place .middle .container .center .chat p.exit {
  font-size: 12px;
  color: #f00;
}
.wrapper .game-place .checks .check {
  height: 20px;
  width: 20px;
  border-radius: 50%;
  position: absolute;
  text-align: center;
  display: none;
}
.wrapper .game-place .checks .check.user1 {
  background-color: #ff7575;
  top: 30px;
  left: 10px;
}
.wrapper .game-place .checks .check.user2 {
  background-color: #7dff7d;
  top: 30px;
  left: 35px;
}
.wrapper .game-place .checks .check.user3 {
  background-color: #66f;
  top: 30px;
  left: 60px;
}
.wrapper .game-place .checks .check.user4 {
  background-color: #ffff80;
  top: 30px;
  left: 85px;
}
.wrapper .game-place .checks .check.user5 {
  background-color: #82ffff;
  top: 30px;
  left: 110px;
}
.wrapper .users {
  height: calc(100vh - 30px);
  width: calc(100vh * 0.4 - 30px);
  border: 1px solid #000;
  box-sizing: border-box;
  margin: 15px 10px;
  flex-shrink: 0;
}
.wrapper .users ul {
  list-style: none;
  display: flex;
  flex-direction: column;
  height: 100%;
}
.wrapper .users ul li {
  border: 1px solid #808080;
  height: 20%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.wrapper .users ul li.user1 {
  background-color: #ff7575;
}
.wrapper .users ul li.user2 {
  background-color: #7dff7d;
}
.wrapper .users ul li.user3 {
  background-color: #66f;
}
.wrapper .users ul li.user4 {
  background-color: #ffff80;
}
.wrapper .users ul li.user5 {
  background-color: #82ffff;
}
.wrapper .users ul li.no-active {
  background-color: #c0c0c0;
}
.wrapper .users ul li p.name {
  font-weight: 700;
  font-size: 20px;
  margin-bottom: 10px;
}
li.item {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 45px;
  color: #808080;
  font-weight: 700;
  background-color: #d4d4d4;
}
li.item.square {
  font-size: 20px;
  font-weight: normal;
  background-color: #fff;
}
